 <script>
import feather from 'feather-icons';
import emailjs from 'emailjs-com';
import { useToast } from "vue-toastification";

export default {
	props: ['showModal', 'modal', 'categories'],
	data() {
		return {
			email: "",
			name: "",
			message: "",
			loading: false,
			serviceID: 'default_service',
			templateID: 'template_30mlgd8',

		}
	},

	methods: {
		sendEmail() {
			this.loading = true
			const toast = useToast();

			emailjs.sendForm('service_6wvo7r8', 'template_seglddb', this.$refs.form, 'lOy6WnAtU4OiI33zT')
				.then((result) => {
					toast.success("Email sent successfully!")
					this.loading = false
					this.email = "",
						this.name = "",
						this.message = "",
						console.log('SUCCESS!', result.text);
				}, (error) => {
					toast.error("An error has occurred, please try again!")
					console.log('FAILED...', error.text);
				});
		}
	},
	mounted() {
		feather.replace();
	},
	updated() {
		feather.replace();
	}

};




</script>

<template>
	<transition name="fade">
		<div v-show="modal" class="font-general-regular fixed inset-0 z-30">
			<!-- Modal body background as backdrop -->
			<div v-show="modal" @click="showModal()"
				class="bg-filter bg-black bg-opacity-50 fixed inset-0 w-full h-full z-20"></div>
			<!-- Modal content -->
			<main class="flex flex-col items-center justify-center h-full w-full">
				<transition name="fade-up-down">
					<div v-show="modal" class="modal-wrapper flex items-center z-30">
						<div
							class="modal max-w-md mx-5 xl:max-w-xl lg:max-w-xl md:max-w-xl bg-secondary-light dark:bg-primary-dark max-h-screen shadow-lg flex-row rounded-lg relative">
							<div
								class="modal-header flex justify-between gap-10 p-5 border-b border-ternary-light dark:border-ternary-dark">
								<h5 class=" text-primary-dark dark:text-primary-light text-xl">
									What project are you looking for?
								</h5>
								<button class="px-4 text-primary-dark dark:text-primary-light" @click="showModal()">
									<i data-feather="x"></i>
								</button>
							</div>
							<div class="modal-body p-5 w-full h-full">
								<form @submit.prevent="sendEmail" ref="form" class="max-w-xl m-4 text-left">
									<div class="mt-0">
										<input
											class="w-full px-5 py-2 border-1 border-gray-200 dark:border-secondary-dark rounded-md text-md bg-secondary-light dark:bg-ternary-dark text-primary-dark dark:text-ternary-light"
											v-model="name" id="name" name="name" type="text" required=""
											placeholder="Name" aria-label="Name" />
									</div>
									<div class="mt-6">
										<input
											class="w-full px-5 py-2 border-1 border-gray-200 dark:border-secondary-dark rounded-md text-md bg-secondary-light dark:bg-ternary-dark text-primary-dark dark:text-ternary-light"
											v-model="email" id="email" name="email" type="email" required=""
											placeholder="Email" aria-label="Email" />
									</div>
									<div class="mt-6">
										<select
											class="w-full px-5 py-2 border-1 border-gray-200 dark:border-secondary-dark rounded-md text-md bg-secondary-light dark:bg-ternary-dark text-primary-dark dark:text-ternary-light"
											id="subject" name="subject" type="text" required=""
											aria-label="Project Category">
											<option v-for="category in categories" :key="category.id"
												:value="category.value">{{ category.name }}</option>
										</select>
									</div>

									<div class="mt-6">
										<textarea
											class="w-full px-5 py-2 border-1 border-gray-200 dark:border-secondary-dark rounded-md text-md bg-secondary-light dark:bg-ternary-dark text-primary-dark dark:text-ternary-light"
											v-model="message" id="message" name="message" cols="14" rows="6"
											aria-label="Details" placeholder="Project description"></textarea>
									</div>

									<div class="mt-6 pb-4 sm:pb-1">
										<button title="Send Request"
											:disabled='email == "" || name == "" || message == ""' class=" disabled:bg-indigo-300 px-4
											sm:px-6
											py-2
											sm:py-2.5
											text-white
											bg-indigo-500
											hover:bg-indigo-600
											rounded-md
											focus:ring-1 focus:ring-indigo-900 duration-500" type="submit" aria-label="Submit Request">
											<div class="flex flex-row items-center gap-2" v-if="loading">Sending
												<svg class="w-5 h-5 text-white animate-spin" fill="none"
													viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
													<circle class="opacity-25" cx="12" cy="12" r="10"
														stroke="currentColor" stroke-width="4">
													</circle>
													<path class="opacity-75"
														d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
														fill="currentColor"></path>
												</svg>
											</div>
											<span v-else>Send Request</span>

										</button>
									</div>
								</form>
							</div>
							<div class="modal-footer mt-2 sm:mt-0 py-5 px-8 border0-t text-right">
								<Button title="Close" class=" px-4
									sm:px-6
									py-2 bg-gray-600 text-primary-light hover:bg-ternary-dark dark:bg-gray-200 dark:text-secondary-dark dark:hover:bg-primary-light
									rounded-md
									focus:ring-1 focus:ring-indigo-900 duration-500" @click="showModal()" aria-label="Close Modal" />
							</div>
						</div>
					</div>
				</transition>
			</main>
		</div>
	</transition>
</template>

<style scoped>
.modal-body {
	max-height: 500px;
}

.bg-gray-800-opacity {
	background-color: #2d374850;
}

@media screen and (max-width: 768px) {
	.modal-body {
		max-height: 400px;
	}
}

.fade-up-down-enter-active {
	transition: all 0.3s ease;
}

.fade-up-down-leave-active {
	transition: all 0.3s ease;
}

.fade-up-down-enter {
	transform: translateY(10%);
	opacity: 0;
}

.fade-up-down-leave-to {
	transform: translateY(10%);
	opacity: 0;
}

.fade-enter-active {
	-webkit-transition: opacity 2s;
	transition: opacity 0.3s;
}

.fade-leave-active {
	transition: opacity 0.3s;
}

.fade-enter,
.fade-leave-to {
	opacity: 0;
}
</style>
